body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/*/css reset/*/
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    font-family: Arial, Helvetica, sans-serif
}

body {
    font-family: inherit;
}

/* Variables */
:root {
    --color-white: white;
    --color-light-white: #eee;

    --color-tomato: blue;
    --color-purple-transparent: rgba(88, 24, 69, .9);
    --color-pink-transparent: rgba(216, 14, 91, .9);

    --color-light-blue: #ff3c00;
  
    --color-orange: #ff3c00;

    --color-light-black: #222;
    --color-black: #000;

    --shadow: rgba(0, 0, 0, .4);
}

/* Layout */
.app {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  
    height: 100vh;
    width: 100%;
    background-color: darkgrey;
    background-size: cover;
  }
  
.calculator {
    height: 80vh;
    width: 25%;
    box-shadow: 0 2rem 4rem rgba(0, 0, 0, .4);
    box-shadow: 0 2rem 4rem var(--shadow);
    background-color: white;
    background-color: var(--color-white);
}

.result-screen {
    height: 75%;
    font-size: 8rem;
    font-weight: 600;
    /* To display the value at the bottom-right of the container */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
  }

  .computation-screen {
    height: 25%;
    font-size: 2.5rem;
    padding-bottom: 5px;
    
    /* To display the value at the bottom-right of the container */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
  }

  .screen {
    height: 25vh;
    width: 100%;
    padding: 0 2rem;
    overflow: hidden;
    white-space: no-wrap;
    color: #eee;
    color: var(--color-light-white);
    background-color: blue;
    background-color: var(--color-tomato);
  }

  .keypad__row {
    height: 11vh; /* (80vh calculator - 25vh screen) / 5 keypadRow components = 11vh */
    width: 100%;
    display: -webkit-flex;
    display: flex; /* To control the dimension */
    color: white;
  }

  .keypad {
    height: 55vh;
    width: 100%;
  }

  .btn {
    width: 25%;
    height: 100%;
    font-size: 3rem;
    font-family: inherit;
    background-color: white;
    background-color: var(--color-white);
    border: 1px solid transparent;
    outline: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transition: all .2s;
    transition: all .2s;
  }
  .btn:hover {
    color: #eee;
    color: var(--color-light-white);
    background-color: blue;
    background-color: var(--color-tomato);
  }
  .btn:active {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }
  .btn--large {
    width: 50%;
    color: white;
    color: var(--color-white);
    background-color: blue;
    background-color: var(--color-tomato);
    -webkit-transition: all .2s;
    transition: all .2s;
  }
  .btn--large:hover {
    background-color: white;
    background-color: var(--color-white);
    color: blue;
    color: var(--color-tomato);
  }
